@import "./common/root" , 
        "./layout/grid" , 
        "./common/common";

html {
    scroll-behavior: smooth;
}

.hide {
    display: none !important;
}

.show {
    display: block !important;
}

.flex {
    display: flex !important;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: clamp(50px,calc(70vw/14.4),100px);
    transition: 0.5s;
    z-index: 7;


    &::before {
        content: '';
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: 0.5s;
        // background-color: rgba(255,255,255,1);
        background-color: rgb(240, 240, 230);

        @include media($sm) {
            background:rgb(240, 240, 230);
        }
    }



    &-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 6;
    }

    &__list {
        display: flex;
        align-items: center;

        @include media($sm){
            position: fixed;
            top: 0;
            left: -100%;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,0.8);
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            z-index: 905;
            transition: 0.5s;
            align-items: start;
            padding: 0 0 0 50px;
            gap: 15px;

            &.active {
                left: 0;
            }
        }
    }


    .info__card:hover .info__title{
        color: red !important;
    }

    &__logo {
        
        & img {
            width: clamp(100px,calc(100vw/14.4),200px);
            height: 100%;
            position: relative;
            z-index: 999;
        }
    }

    &__link {
        display: inline-block;
        margin: 0 15px;
        text-transform: uppercase;
        color: #000;

        @include media($sm) {
            color: #fff;
            font-size: 32px;
        }
    }


    &__burger {
        width: 30px;
        height: 20px;
        overflow: hidden;
        position: relative;
        display: none;
        
        &.active::before {
            top: 8px;
            transform: rotate(45deg);
            width: 80%;
        }
        &.active::after {
            top: 8px;
            transform: rotate(-45deg);
            width: 80%;
        }
        &.active span {
            transform: scaleX(0);
        }
        


        @include media($sm){
            display: block;
            z-index: 999;
            
        }

        &::before, 
        &::after, 
        & span {
            position: absolute;
            right: 0;
            display: block;
            height: 2px;
            background: black;
            transition: 0.5s;
        }
        &::before,
        &::after {
            content: '';
        }
        &::before {
            top: 0;
            width: 100%;
        }
        &::after {
            bottom: 0;
            width: 40%;
        }
        & span {
            top: 9px;
            width: 70%;
            transition: 0.1s;
            
        }
    }
   
}


.start {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    text-align: center;
    // justify-content: center;

    &__title {
        // color: rgb(241, 28, 28);
        color: white;
        font-size: 25px;
        margin-bottom: 20px;
        font-family: 'avenir';
        text-transform: uppercase;
        // border: 1px solid white;
        // -webkit-text-stroke: 1px white;

    }


    &__text {
        color: white;
        font-family: 'avenir';
    }

    &__link {
        font-family: 'avenir';
        color: white;
        background: rgb(173, 46, 46);
        border-radius: 10px;
        padding: 10px 25px;
        margin-top: clamp(10px,calc(20vw/14.4),25px);
        cursor: pointer;
        border: none;
    }



    &-wrapper {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
    }

    &-slide {
        flex: 1 0 100%;
        height: 100%;
        position: relative;


        & img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: 0 -50px;

            @media(max-aspect-ratio: 3/2){
                object-position: 50% top;
            }
        }

        & .container {
            // width: 600px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

        &__wrapper {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: relative;
            z-index: 995;
            align-items: center;
        }
    }

    &-pagination {
        position: absolute;
        bottom: 20px;
        left: 0;
        width: 100%;
        display: flex;
        justify-content: center;

        @include media($xs) {
            bottom: 60px !important;
        }
    }
}

.red_text {
    color: red;
    -webkit-text-stroke: 0.5px white;
}


.info {
    background: rgb(241, 241, 236);
    padding: 70px 0;
    width: 100%;



    &__wrapper {
        
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        gap: 25px;

        @include media($sm){
            flex-direction: column;
            align-items: center;
        }
    }

    &__card {
        display: flex;
        flex-direction: column;
        padding: 15px 20px;
        gap: 30px;
        background: #fff;
        position: relative;
        border-radius: 20px;
        flex: 1;
        
        // width: 400px;
        // overflow: hidden;


        .card_bg {
            width: 300px;
            position: absolute;
            right: 0;
            bottom: 0;
        }
        

        @include media-min($sm) {
            &:hover::before {
                top: 0;
                transform: scale(1);
            }
            &:hover * {
                // color: red;
            }
        }

        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            top: -40%;
            left: 0;
            background: rgb(216, 235, 206);
            transition: 0.3s;
            transform: scale(0);
            border-radius: 20px;
            
        }
    }

    &__title,
    &__text,
    &__link {
        position: relative;
        transition: 0.2s;
    }
}



.step {
    width: 100%;
    padding: 100px 0 60px 0;
    background: #d8ebce;
    position: relative;

    &__wrapper {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    
    &__list {
        display: flex;
        
        & li {
            width: calc(100%/3);
            border: 1px solid #000;
            border-radius: 20px;
            text-align: center;
            cursor: pointer;
            padding: 15px 0;

            &.active {
                background: rgb(225, 248, 252);
            }
        }
    }

    &__contents {
        padding: 15px 20px;
        font-size: 24px;
        text-align: center;
    }

    &__content {
        animation: tabContent 0.5s ease-in;
    }

    @keyframes tabContent {
        from {
            transform: translateY(100px);
            opacity: 0;
        }
        to {
            transform: translateY(0px);
            opacity: 1;
        }
    }
}


.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: rgba(0,0,0,0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 900;

    &__content {
        position:relative;
        width:100%;
        padding:40px;
        background-color:#fff;
        border:1px solid rgba(0,0,0,.2);
        border-radius:4px;
        max-height:80vh;
        overflow-y:auto;
    }

    &__close {
        position:absolute;
        top:8px;
        right:14px;
        font-size:30px;
        color:#000;
        opacity:.5;
        font-weight:700;
        border:none;
        background-color:transparent;
        cursor:pointer;
    }
}

.call {
    position: fixed;
    font-size: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 20px;
    bottom: 20px; 
    // padding: 15px 10px;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background: rgb(173, 46, 46);
    color: white;
    cursor: pointer;
    z-index: 800;

    @include media($sm) {
        width: 100%;
        right: 0;
        border-radius: 0;
        background: rgb(173, 46, 46);
        color: white;
        bottom: 0;
        height: 50px;
    }

}



.attach {
    padding: 100px 0;
    background: url('../img/fixedBg.jpg') center center / cover no-repeat;
    background-attachment: fixed;

    &__body {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}


.client {
    height: 250px;
    width: 100%;
    padding: clamp(15px,calc(50vw/14.4),60px) 0;

    &__body {
        height: 100%;
    }

    &__body-swiper {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    &__slide {
        // border: 1px solid red;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
            width: 80%;
            object-fit: contain;

            @include media($xs) {
                width: 60%;
            }
        }
    }

    &__title {
        text-align: center;

    }
}

.attach__title {
    color: rgb(173, 46, 46);
    -webkit-text-stroke: black 0.5px;
    // -webkit-text-stroke: white 10px ;
}

.card__logo {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 200px;
}

.col {
    padding: 70px 0;

    &__body {
        display: flex;
        gap: 20px;
        flex-wrap: wrap;
    }

    &__main-title {
        text-align: center;
        margin-bottom: 20px;
    }

    &__card {
        position: relative;
        height: 200px;
        flex: 1 0 30%;
        padding: 15px;
        // border: 1px solid #000;
        transition: 0.5s;
        background: #f1f1ec;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 20px;

        .col__title {
            text-align: center;
        }

        &:hover {
            transform: scale(1.1);
            box-shadow: 19px 16px 5px -5px rgba(196, 202, 206, 0.6);
            z-index: 999;
            

        }

        @include media($sm) {
            flex: 1 0 45%;
        }
        @include media($xs) {
