        }
    }
}


.address {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 25px;

    &__body{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    &__map {
        width: 100%;
        height: 50vh;
        position: relative;
        & iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 2;
    
        }
    }
}

.footer {
    background: #000;
    padding: 20px 10px 20px 15px;
    &__body {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__text {
        text-align: center;
        color: #fff;
    }
    
    &__copyright {
        font-family: 'futura-bk';
        color: white;

    }

}

.preloader {
      /*фиксированное позиционирование*/
  position: fixed;
  /* координаты положения */
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  /* фоновый цвет элемента */
  background: #e0e0e0;
  /* размещаем блок над всеми элементами на странице (это значение должно быть больше, чем у любого другого позиционированного элемента на странице) */
  z-index: 1001;

  &__row {
    position: relative;
    top: 50%;
    left: 50%;
    width: 70px;
    height: 70px;
    margin-top: -35px;
    margin-left: -35px;
    text-align: center;
    animation: preloader-rotate 2s infinite linear;
  }

  &__item {
    position: absolute;
    display: inline-block;
    top: 0;
    background-color: #337ab7;
    border-radius: 100%;
    width: 35px;
    height: 35px;
    animation: preloader-bounce 2s infinite ease-in-out;

    &:last-child {
        top: auto;
        bottom: 0;
        animation-delay: -1s;
    }
  }

}

@keyframes preloader-rotate {
    100% {
      transform: rotate(360deg);
    }
}
  
@keyframes preloader-bounce {
  0% {
      transform: scale(0);
  }
  100% {
      transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}

.loaded_hiding .preloader {
  transition: 0.3s opacity;
  opacity: 0;
}

.loaded .preloader {
  display: none;
}
