$Montserrat: ("Boho"), 
            ("Bubblegum"),
            ("Cantora"),
            ("Futura"),
            ("Gochi"),
            ("Inknut"),
            ("Kurale"),
            ("Bruno"),
            ("Maximian"),
            ("Rapidtech"),
            ("RedRose"),
            ("Righteous"),
            ("Roboto"),
            ("Titan");

            

@each $family in $Montserrat {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}.ttf);
    }
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}.otf);
    }
}   

@font-face {
    font-family: 'avenir';
    src: url(../../fonts/AvenirNext.ttc);
}
@font-face {
    font-family: 'cantora';
    src: url(../../fonts/CantoraOne-Regular.ttf);
}
@font-face {
    font-family: 'futura-reg';
    src: url(../../fonts/57Futura-Regular.otf);
}
@font-face {
    font-family: 'futura-bk';
    src: url(../../fonts/FuturaBkBTBook.ttf);
}


$FontAwesome: ("fontawesome","duotone"),
            ("fontawesome","light"),
            ("fontawesome","brands");

@each $family,$type in $FontAwesome {
    @font-face {
        font-family: 'fontawesome';
        src: url(../fonts/#{$family}/#{$family}-#{$type}.ttf);
    }
}